import React, {FunctionComponent, useState} from 'react';
import GridLayout from 'react-grid-layout';
import '../../node_modules/react-grid-layout/css/styles.css'
import '../../node_modules/react-resizable/css/styles.css'
import ChartPie from "../components/ChartPie";
import {Button, Paper} from "@material-ui/core";


const DashboardBuilder: FunctionComponent<{}> = (props) => {
    const [layout, setLayout] = useState<any>(
        [
            {
                "w": 7,
                "h": 6,
                "x": 0,
                "y": 0,
                "i": "a",
                "minW": 2,
                "minH": 2,
                "moved": false,
                "static": false
            },
            {
                "w": 5,
                "h": 6,
                "x": 0,
                "y": 8,
                "i": "b",
                "minW": 5,
                "maxW": 6,
                "minH": 2,
                "moved": false,
                "static": false
            },
            {
                "w": 4,
                "h": 6,
                "x": 5,
                "y": 6,
                "i": "c",
                "minW": 2,
                "maxW": 4,
                "minH": 2,
                "moved": false,
                "static": false
            },
            {
                "w": 5,
                "h": 6,
                "x": 7,
                "y": 0,
                "i": "0.28252354517198475",
                "minW": 2,
                "minH": 2,
                "moved": false,
                "static": false
            },
            {
                "w": 12,
                "h": 4,
                "x": 0,
                "y": 14,
                "i": "0.7801416141073108",
                "minW": 2,
                "minH": 2,
                "moved": false,
                "static": false
            },
            {
                "w": 3,
                "h": 6,
                "x": 9,
                "y": 6,
                "i": "0.10454900057780647",
                "minW": 2,
                "minH": 2,
                "moved": false,
                "static": false
            },
            {
                "w": 1,
                "h": 2,
                "x": 4,
                "y": 6,
                "i": "0.5135486563729574",
                "minW": 2,
                "minH": 2,
                "moved": false,
                "static": false
            },
            {
                "w": 4,
                "h": 2,
                "x": 0,
                "y": 6,
                "i": "0.8272674430406111",
                "minW": 2,
                "minH": 2,
                "moved": false,
                "static": false
            }
        ]
    )
    const handleAdd = () => {
        const layoutClone = JSON.parse(JSON.stringify(layout))
        console.log(layoutClone)
        setLayout(
            [{
                i: String(Math.random()),
                x: 0,
                y: 0,
                w: 4,
                h: 8,
                minW: 2,
                minH: 4,
                component: 'ChartPie'
            },...layoutClone ]
        )
    }
    const layoutChange = (layout:any) =>{
        setLayout(layout);
        console.log(layout);
    }
    const Components = [<ChartPie />, <ChartPie />, <ChartPie />]
    return (
        <>
            <Button onClick={handleAdd}>add</Button>
            <GridLayout
                className="layout"
                cols={12}
                rowHeight={30}
                width={1200}
                layout={layout}
                onLayoutChange={layoutChange}
            >
                {layout.map((Element: any, index: number) =>
                    <Paper key={Element.i}>
                        <ChartPie />
                    </Paper>
                )}
            </GridLayout>
        </>
    )
}

export default DashboardBuilder;
